<style>
  @import "../../../static/style/login.css";
</style>

<template>
  <div class="login-wraper">
    <!---->
    <div class="login-form-box">
      <div class="form-title">-</div>
      <div class="form-title-line"></div>
      <el-form :model="loginFormData" ref="loginForm" label-width="0">

        <el-form-item>
          <el-input placeholder="" v-model="loginFormData.username" @focus="hideErrorTipHandle" @keyup.enter.native="submitLoginForm">
            <template slot="prepend">
              <i class="fa fa-user" aria-hidden="true" style="width: 14px;font-size: 16px;text-align: center;"></i>
            </template>
          </el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input placeholder="" type="password" v-model="loginFormData.password" @focus="hideErrorTipHandle" @keyup.enter.native="submitLoginForm">
            <template slot="prepend">
              <i class="fa fa-lock" aria-hidden="true" style="width: 14px;font-size: 16px;text-align: center;"></i>
            </template>
          </el-input>
        </el-form-item>

        <div class="identify-code-wraper" v-if="false">
          <el-form-item style="flex:1;">
            <el-input placeholder="" v-model="loginFormData.identifyCode" @focus="hideErrorTipHandle" @keyup.enter.native="submitLoginForm">
              <template slot="prepend">
                <i class="fa fa-key" aria-hidden="true"></i>
              </template>
            </el-input>
          </el-form-item>
          <div class="identify-code-box">
            <img :src="codeImg" alt="" class="code-img" height="44px" width="106px" @click="getVerification">
          </div>
        </div>

        <el-form-item>
          <el-button style="width:100%;" class="login-btn" type="primary" @click="submitLoginForm" :disabled="isDisabled" :loading="isDisabled"> </el-button>
        </el-form-item>
        <div class="error-tip-box" v-show="isErrorTipShow">{{ this.errorTipMesg }}</div>
        <!-- <div class="login-form-footer">
          <p class="forget-pass" @click="goAddress('/forget_password')"></p>
          <div class="quick-login">
            <p @click="goAddress('/sacn_code_login')"></p>
            <p @click="goAddress('/phone_login')"></p>
          </div>
        </div> -->
      </el-form>
    </div>
    <!--logo-->
    <div class="logo-wrap">
      <img src="/shop/static/logo.png" alt="">
    </div>
    <!---->
    <div class="footer-wraper">
      2017-2018 Benlai ICP120338     11010502022386
    </div>
  </div>

</template>

<script>
import commonUrl from '@/api/Common.js'
import Uuid from 'uuid/v4'
import axios from 'axios'
import Storage from '@/utils/localStorage'
export default{
  data() {
    return {
      isDisabled: false,
      uuidVal: '', // 
      isErrorTipShow: false, // 
      loginFormData: {
        username: '',
        password: '',
        identifyCode: 'test'
      },
      errorTipMesg: 'no error tip',
      codeImg: '',
      allMenus: []
    }
  },
  created() {
    // uuid
    this.uuidVal = Uuid()
    // 
    if(Storage.get('loginInfo')) {
      this.$router.push("/index")
    }
    // 
    let statusVal = this.$route.query.status
    if(statusVal == 401) {
      this.$notify({
        title: '',
        message: '',
        type: 'warning'
      })
      return
    }else if(statusVal == 'pok') {
      this.$notify({
        title: '',
        message: ''
      })
      return
    }
  },
  mounted() {
    // 
    // this.getVerification()
  },
  components: {

  },
  methods: {
    /**
     * [getVerification ]
     */
    getVerification() {
      let params = new URLSearchParams()
      params.append('uuid', this.uuidVal)
      axios.post(commonUrl.getVerificationImage, params, {responseType: 'arraybuffer'}).then(res => {
        this.codeImg = 'data:image/png;base64,' + new Buffer(res.data, 'binary').toString('base64')
      }).catch(e => {})
    },
    /**
     * [doLogin ]
     * @return {[type]} [description]
     */
    submitLoginForm() {
      if(this.loginFormData.username === '') {
        this.isErrorTipShow = true
        this.errorTipMesg = ''
        return
      }
      if(this.loginFormData.password == '') {
        this.isErrorTipShow = true
        this.errorTipMesg = ''
        return
      }
      if(this.loginFormData.identifyCode == '') {
        this.isErrorTipShow = true
        this.errorTipMesg = ''
        return
      }
      let params = new URLSearchParams()
      let headers = {
        'Authorization': 'Basic d2ViOmQ4YmZjMzQwMWE3NTg5ZTc4NGIwNmJkZmdhMmFkMWM0ZQ=='
      }
      params.append('username', this.loginFormData.username)
      params.append('password', this.loginFormData.password)
      params.append('grant_type', 'password')
      params.append('verificationCode', this.loginFormData.identifyCode)
      params.append('uuid', this.uuidVal)

      this.isDisabled = true
      axios.post(commonUrl.token, params, {headers: headers}).then(res => {
        setTimeout(() => {
          this.isDisabled = false
        }, 800)
        let data = res.data
        if (!data || !data.token_type || !data.access_token) {
          return res
        }
        // 
        Storage.remove('loginInfo')
        let selectstore = {}
        if(data.selectstore) {
          selectstore = JSON.parse(data.selectstore)
        }
        Storage.set('loginInfo', {
          StoreName: selectstore.storeName,
          Operator: data['user:name'],
          UserNO: data['user:name'],
          UserName: data['user:nickname'],
          token_type: data.token_type,
          StoreSysNo: selectstore.storeSysNo,
          StoreNO: selectstore.storeNo,
          access_token: data.access_token,
          refresh_token: data.refresh_token,
          IsFirstLogin: data.isFirstLogin === '1',
          IsDC: true,
          net_token: data.net_token,
          dashboardPermission: data['user:dashboardPermission'],
          userName: data['user:name'],
          userNickname: data['user:nickname'],
          isFirstLogin: data.isFirstLogin === '1',
          expiresTime: data['user:expires'],
          selectStore: data.selectstore,
          loginTime: new Date().getTime()
        })
        this.$store.state.userName = data['user:nickname']
        this.$store.dispatch('common/GET_REGION_LIST')
        // 
        setTimeout(() => {
          this.$router.push('/index')
        }, 300);
      }).catch(({response}) => {
        this.isDisabled = false
        // 
        // this.getVerification()
        if(response) {
          let msg = response.data.error_description || response.statusText
          this.isErrorTipShow = true
          this.errorTipMesg = msg
        }
      })
    },
    /**
     * [hideErrorTipHandle ]
     * @return {[type]} [description]
     */
    hideErrorTipHandle() {
      this.isErrorTipShow = false;
    },
    /**
     * [goAddress ]
     * @param  {[type]} _path []
     * @return {[type]}       [description]
     */
    goAddress(_path) {
      if(_path) {
        this.$router.push(_path)
      }
    }
    
  }

}
</script>



