import axios from 'axios'
import Storage from './localStorage'
import config from './config'
// baseUrl
let baseUrl = config.reqUrl
// 
axios.defaults.timeout = 60000;
// 
axios.defaults.withCredentials = true;

let requestMap = {}
/**
 * [setHeaders ]
 * @return {[type]}          [description]
 */
function setHeaders () {
  let loginInfo = Storage.get('loginInfo') || {}
  let headers = {}
  headers['Authorization'] = `${loginInfo.token_type} ${loginInfo.access_token}`
  headers['device_type'] = 'web'
  headers['version'] = '2.0.1'
  headers['Content-Type'] = 'application/json;charset=UTF-8'
  return headers
}

/**
 * [handleError ]
 * @param  {[type]} response [description]
 * @return {[type]}          [description]
 */
function handleError (response) {
  if(response == undefined) {
    // Provisional headers are shown 
    return {code: "Provisional headers are shown", msg: '', status: 504}
  }
  if (response.status === 400) {
    return {code: response.data.error, msg: response.data.error_description, status: response.status}
  }
  if (response.status === 401) {
    Storage.remove('loginInfo')
    sessionStorage.clear()
    window.location.replace('/shop/login?status=401')
    return
  }
  if (response.status === 403) {
    return {code: response.data.error, msg: response.data.error_description, status: response.status}
  }
  if (response.status === 404) {
    return {code: response.data.error, msg: "", status: response.status}
  }
  if (response.status === 500) {
    return {code: response.data.error, msg: "", status: response.status}
  }
  if (response.status === 504) {
    return {code: response.data.error, msg: "", status: response.status}
  }
  return {
    code: response.data.error,
    msg: response.data.error_description || response.statusText,
    status: response.status
  }
}

/**
 * [get ajax get]
 * @param  {[type]}   url      [description]
 * @param  {Object}   params   [description]
 * @param  {Function} callback [description]
 * @return {[type]}            [description]
 */
function get (url, params = {}, callback) {
  url = baseUrl + url
  let key = url + '_' + JSON.stringify(params)
  if (requestMap[key]) {
    return requestMap[key]
  }

  let res = {}
  let promise = axios.get(url, { params: params, headers: setHeaders() }).then(function (response) {
    delete requestMap[key]
    let res = response.data
    return res
  }).catch(function ({response}) {
    delete requestMap[key]
    return handleError(response)
  })
  if (callback) {
    promise = promise.then(callback)
  }

  requestMap[key] = promise
  return promise
}

/**
 * [post ajax post]
 * @param  {[type]}   url      [description]
 * @param  {Object}   params   [description]
 * @param  {Function} callback [description]
 * @param DIFFCITY {Bollean}  true: cityIdcityName  falsefalsecityIdcityName
 * @return {[type]}            [description]
 */
function post (url, params = {}, callback) {
  if(Storage.get("cityId") && Storage.get("cityName") && !params.DIFFCITY) {
    params.cityId = Storage.get("cityId")
    params.cityName = Storage.get("cityName")
  }
  url = baseUrl + url
  let key = url + '_' + JSON.stringify(params)
  if (requestMap[key]) {
    return requestMap[key]
  }

  let promise = axios.post(url, params, { headers: setHeaders() }).then(function (response) {
    delete requestMap[key]
    let res = response.data
    return res
  }).catch(function ({response}) {
    delete requestMap[key]
    return handleError(response)
  })
  if (callback) {
    promise = promise.then(callback)
  }

  requestMap[key] = promise
  return promise
}

export default {
  get,
  post
}



