// 
const MemberData = () => import('@/components/Report/MemberData')
// 
const FinData = () => import('@/components/Report/FinData')

const reportRouter = [
  {
    path: '/mdata',
    name: '',
    meta: {
      title: '',
      scrollToTop: true,
      breadcrumbList: [{
        name: '',
        path: ''
      },
      {
        name: '',
        path: ''
      }
      ]
    },
    component: MemberData
  },
  {
    path: '/findata',
    name: '',
    meta: {
      title: '',
      scrollToTop: true,
      breadcrumbList: [{
        name: '',
        path: ''
      },
      {
        name: '',
        path: ''
      }
      ]
    },
    component: FinData
  }
]

export default {
  reportRouter
}



