<template>
  <div>
    <el-dialog 
      title="" 
      :visible.sync="dialogFormVisible" 
      width='460px'
      :close-on-click-modal="!isFirstLogin"
      :close-on-press-escape="!isFirstLogin"
      :show-close="!isFirstLogin">
      <el-form 
        :model="changePassMesg" 
        :rules="rules" 
        ref="changePassMesg"
        style="width:410px;">
        <el-form-item label="" :label-width="formLabelWidth" required prop="oldPasswd">
          <el-input type="password" size="small" v-model="changePassMesg.oldPasswd" autocomplete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" size="small" :label-width="formLabelWidth" required prop="newPasswd">
          <el-input type="password" v-model="changePassMesg.newPasswd" autocomplete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" size="small" :label-width="formLabelWidth" required prop="comfirmNewPasswd">
          <el-input type="password" v-model="changePassMesg.comfirmNewPasswd" autocomplete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" size="small" :label-width="formLabelWidth">
          <p v-if="isFirstLogin" style="color: red;"></p>
          <p></p>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false" size="mini" v-if="!isFirstLogin"> </el-button>
        <el-button type="primary" @click="doChangePassHandel" size="mini" :disabled = "isDisabled"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import Storage from '@/utils/localStorage'
  let Base64 = require('js-base64').Base64
  const loginUrl = {
    modifyPwd: '/foundation/user/selfPassword/update'
  }
  export default {
    data() {
      let reg = /^(?=.*[A-Z])(?=.*[a-z])(?=.*\d)[A-Za-z\d`~!@#$%^&*()_+-=,.?]{8,20}$/
      let validPwd = (rule, value, callback) => {
        if (!value.trim()) {
          return callback(new Error(''))
        }
        if (!(reg.test(value))) {
          return callback(new Error('8-20'))
        }
        callback()
      }
      let validPwd2 = (rule, value, callback) => {
        if (!value.trim()) {
          return callback(new Error(''))
        }
        if (value !== this.changePassMesg.newPasswd) {
          return callback(new Error(''))
        }
        callback()
      }
      return {
        isDisabled: false,
        // 
        formLabelWidth: '90px',
        dialogFormVisible: false,
        routeList: [],
        changePassMesg: {
          oldPasswd: '',
          newPasswd: '',
          comfirmNewPasswd: ''
        },
        rules: {
          oldPasswd: [
            { required: true, message: '', trigger: 'blur' }
          ],
          newPasswd: [
            { required: true, message: '', trigger: 'blur' },
            {validator: validPwd, trigger: 'blur'}
          ],
          comfirmNewPasswd: [
            { required: true, message: '', trigger: 'blur' },
            {validator: validPwd2, trigger: 'blur'}
          ]
        },
        isFirstLogin: Storage.get('loginInfo') ? Storage.get('loginInfo').isFirstLogin : false
      }
    },
    methods: {
      setDefault() {
        this.dialogFormVisible = true
      },
      /**
       * [doChangePassHandel ]
       * @return {[type]} [description]
       */
      doChangePassHandel() {
        this.$refs.changePassMesg.validate((valid) => {
          if(valid) {
            let _params = {
              oldPasswd: Base64.encode(this.changePassMesg.oldPasswd),
              newPasswd: Base64.encode(this.changePassMesg.newPasswd),
              comfirmNewPasswd: Base64.encode(this.changePassMesg.comfirmNewPasswd)
            }
            this.isDisabled = true
            this.$http.post(loginUrl.modifyPwd, { ..._params }).then(res => {
              setTimeout(() => {
                this.isDisabled = false
              }, 800)
              if (res.code === 0) {
                localStorage.removeItem('loginInfo')
                window.location.replace('/shop/login?status=pok')
                return
              }
              throw new Error(res.msg)
            }).catch(e => {
              this.isDisabled = false
              this.$notify.error({
                title: '',
                message: e.message
              })
            })
          }else {
            return false
          }
        })

      }
    }
  }
</script>


