const errorRouter = [
  {
    path: '/error',
    name: '',
    meta: {
      title: '',
      scrollToTop: true,
      cantCheck: true,
      breadcrumbList: [
        {name: '', path: '/'},
        {name: '', path: '/error'}
      ]
    },
    component: () => import('@/components/Common/ErrorPage/index')
  },
  {
    path: '/noopt',
    name: '',
    meta: {
      title: '',
      scrollToTop: true,
      cantCheck: true,
      breadcrumbList: [
        {name: '', path: '/'},
        {name: '', path: ''}
      ]
    },
    component: () => import('@/components/Common/NoOpt/index')
  }
]

export default {
  errorRouter
}




