// state
const state = {
  buyerPhone: ''
}

// getters
const getters = {
  cartProducts: (state, getters, rootState) => {
    return state.items.map(({ id, quantity }) => {
      const product = rootState.products.all.find(product => product.id === id)
      return {
        title: product.title,
        price: product.price,
        quantity
      }
    })
  },

  cartTotalPrice: (state, getters) => {
    return getters.cartProducts.reduce((total, product) => {
      return total + product.price * product.quantity
    }, 0)
  }
}

// actions
const actions = {
  setByUserphone({ commit, state }, param) {
    commit('setByUserphone', param)
  }
}

// mutations
const mutations = {
  setByUserphone(state, param) {
    state.buyerPhone = param.buyerPhone
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}



