import ajax from "@/utils/request"
import Storage from "@/utils/localStorage"
const requestUrl = '/foundation/storeGroup/userAreaStoreGroupList'
// state
const state = {
  loading: '',
  userPermissionMap: {},
  cityOptions: [],
  regionList: [],
  currentDate: new Date().getTime()
}

// actions
const actions = {
  showLoading({ commit, state }, param) {
    commit('showLoading', param)
  },
  setUserOptions({commit, state}, param) {
    commit('setUserOptions', param)
  },
  setCityOptions({commit, state}, param) {
    commit('setCityOptions', param)
  },
  GET_REGION_LIST({commit, state}, param) {
    ajax.get(requestUrl).then(res => {
      if(res.code === 0) {
        let regionList = res.data && res.data.length ? res.data : []
        if(Storage.get('regionCode')) {
          let hasRegionOpt = regionList.some(item => {
            return item.storeGroupNo === Storage.get('regionCode')
          })
          if(!hasRegionOpt) {
            let regionCode = regionList[0] && regionList[0].storeGroupNo ? regionList[0].storeGroupNo : ''
            Storage.set('regionCode', regionCode)
          }
        }else {
          let regionCode = regionList[0] && regionList[0].storeGroupNo ? regionList[0].storeGroupNo : ''
          Storage.set('regionCode', regionCode)
        }
      }
    })
  },
  REGION_CHANGE({commit, state}, param) {
    let currentDate = new Date().getTime()
    commit('SET_CURRENT_DATE', currentDate)
  }
}

// mutations
const mutations = {
  showLoading(state, param) {
    state.loading = param.showLoading
  },
  setUserOptions(state, param) {
    state.userPermissionMap = param
  },
  setCityOptions(state, param) {
    state.cityOptions = param.cityOptions
  },
  SET_CURRENT_DATE(state, param) {
    state.currentDate = param
  }
}

const getters = {
  getPermissionMap: (state, getters) => {
    return state.userPermissionMap
  },
  getCityOptions: (state, getters) => {
    return state.cityOptions
  },
  GET_CURRENT_DATE(state, getters) {
    return state.currentDate
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations,
  getters
}



