const CommonReport = () => import('@/components/Common/CommonReport')
const CompoundReport = () => import('@/components/Common/CommonReport/compoundReport')

const commonReportRoutersList = [
  {
    path: '/report/:id',
    name: '',
    meta: {
      title: '',
      scrollToTop: true,
      breadcrumbList: [
        {name: '', path: '/'},
        {name: '', path: ''}
      ]
    },
    component: CommonReport
  },
  {
    path: '/preview/:id',
    name: '',
    meta: {
      title: '',
      scrollToTop: true,
      cantCheck: true,
      breadcrumbList: [
        {name: '', path: '/'},
        {name: '', path: ''}
      ]
    },
    component: CommonReport
  },
  {
    path: '/mixReport/:pid',
    name: '',
    meta: {
      title: '',
      scrollToTop: true,
      breadcrumbList: [
        {name: '', path: '/'},
        {name: '', path: ''}
      ]
    },
    component: CompoundReport
  },
  {
    path: '/mixReport/:pid/:id',
    name: '',
    meta: {
      title: '',
      scrollToTop: true,
      breadcrumbList: [
        {name: '', path: '/'},
        {name: '', path: ''}
      ]
    },
    component: CompoundReport
  },
  {
    path: '/previewMix/:pid',
    name: '',
    meta: {
      title: '',
      scrollToTop: true,
      cantCheck: true,
      breadcrumbList: [
        {name: '', path: '/'},
        {name: '', path: ''}
      ]
    },
    component: CompoundReport
  },
  {
    path: '/previewMix/:pid/:id',
    name: '',
    meta: {
      title: '',
      scrollToTop: true,
      cantCheck: true,
      breadcrumbList: [
        {name: '', path: '/'},
        {name: '', path: ''}
      ]
    },
    component: CompoundReport
  }
]

export default {
  commonReportRoutersList
}




