/**
 * 
 * @description: 
 * @param {fmt}  yyyy/MM/dd HH:mm:ss 
 * @param {_timestamp}  3 101000
 * @return: 
 */
export function dateFormat (fmt, _timestamp) {
  if(_timestamp.toString().length === 10) {
    _timestamp = _timestamp * 1000
  }
  let date = new Date(_timestamp)
  let ret;
  let opt = {
    "y+": date.getFullYear().toString(),
    "M+": (date.getMonth() + 1).toString(),
    "d+": date.getDate().toString(),
    "H+": date.getHours().toString(),
    "m+": date.getMinutes().toString(),
    "s+": date.getSeconds().toString()
  };
  for (let k in opt) {
    ret = new RegExp("(" + k + ")").exec(fmt);
    if (ret) {
      fmt = fmt.replace(ret[1], (ret[1].length === 1) ? (opt[k]) : (opt[k].padStart(ret[1].length, "0")))
    };
  };
  return fmt;
}

/**
 * isKeyInArray key
 * @param k {String} key
 * @param array {Array} 
 * @returns {boolean} true   false
 */
export function isKeyInArray (k, array) {
  let val = false
  for (let item of array) {
    if (val) {
      break
    }
    if (typeof (item) === 'object') {
      for (let key of Object.keys(item)) {
        if (k === item[key]) {
          val = true
          break
        }
      }
    }
  }
  return val
}

/**
 * removeItemByKey key
 * @param k {String}
 * @param array {Array}
 */
export function removeItemByKey (k, array) {
  array.forEach((item, index) => {
    if (typeof (item) === 'object') {
      for (let key of Object.keys(item)) {
        if (k === item[key]) {
          array.splice(index, 1)
          break
        }
      }
    }
  })
}

/**
 * findIndexByKey key
 * @param k {String}
 * @param array {Array}
 * @returns {number} 
 */
export function findIndexByKey (k, array) {
  let val = -1
  array.forEach((item, index) => {
    if (typeof (item) === 'object') {
      for (let key of Object.keys(item)) {
        if (k === item[key]) {
          val = index
          break
        }
      }
    }
  })
  return val
}

/**
 * getUrlPath urlpath
 * @returns {string}
 */
export function getUrlPath () {
  const url = location.href
  let path = url.slice(url.lastIndexOf('/') + 1, url.indexOf('?') === -1 ? url.length : url.indexOf('?'))
  return path
}

/**
 * @param obj {Object} {a: 1, b: 2}
 * @returns {string} key=value&key=value
 */
export function toUrlFormat (obj) {
  let str = ''
  for (let key of Object.keys(obj)) {
    str += obj[key] && `${key}=${obj[key]}&`
  }
  str = str.substr(0, str.length - 1)
  return str
}


/**
 * toUpperFirstLetter json
 * @param obj {Object}
 * @returns {Object}
 */
export function toUpperFirstLetter (obj) {
  const newObj = {}
  for (let key of Object.keys(obj)) {
    let newKey = key.substr(0, 1).toUpperCase() + key.substr(1)
    newObj[newKey] = obj[key]
  }
  return newObj
}

/**
 * formatDate  yyyy-MM-dd iosutcgmt8  2017-11-30 
 * @param str {String}
 * @param seconds
 * @returns {string}
 */
export function formatDate (str, seconds) {
  if (!str) {
    return ''
  }
  function parse (n) {
    return n < 10 ? ('0' + n) : n
  }
  let dt = new Date(str)
  let y = dt.getFullYear()
  let m = dt.getMonth() + 1
  let d = dt.getDate()
  if (!seconds) {
    return [y, parse(m), parse(d)].join('-')
  } else {
    let h = dt.getHours()
    let M = dt.getMinutes()
    let s = dt.getSeconds()
    return [y, parse(m), parse(d)].join('-') + ' ' + [parse(h), parse(M), parse(s)].join(':')
  }
}

/**
 * toPrice 
 * @param num
 * @returns {string}
 */
export function toPrice (num) {
  return '' + parseFloat(num).toFixed(2)
}


/**
 * isWeChat 
 * @returns {boolean}
 */
export function isWeChat () {
  return !!navigator.userAgent.match(/MicroMessenger/i)
}

/**
 * isMobile 
 * @returns {boolean}
 */
export function isMobile () {
  return !!navigator.userAgent.match(/(iPhone|iPod|Android|iOS|iPad|Mobile)/i)
}


/**
 * [timestampToTime ]
 * @param    {[type]}                 timestamp [description]
 * @return   {[type]}                           [description]
 */
export function timestampToTime(timestamp) {
  var date = new Date(timestamp);//10*1000131000
  var Y = date.getFullYear() + '-';
  var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
  var D = date.getDate() < 10 ? ('0' + date.getDate() + ' ') : date.getDate() + ' ';
  var h = date.getHours() < 10 ? ('0' + date.getHours() + ':') : date.getHours() + ':';
  var m = date.getMinutes() < 10 ? ('0' + date.getMinutes() + ':') : date.getMinutes() + ':';
  var s = date.getSeconds() < 10 ? ('0' + date.getSeconds()) : date.getSeconds();
  return Y + M + D + h + m + s;
}

