/**
 * [moneyToFixed ]
 * @param  {[type]} _money [description]
 * @return {[type]}        [description]
 */
export function moneyToFixed(_money) {
  if(_money == null || _money == undefined) {
    return
  }
  if(_money != "") {
    return parseFloat(_money).toFixed(2)
  }else {
    return '0.00'
  }
}

