/**
 * [compareDate ]
 * @param  {[type]} _startTime [ ]
 * @param  {[type]} _endTime   []
 * @return {[type]}            [description]
 */
export function compareDate(_startTime, _endTime) {
  let startTime = _startTime.sort()
  let endTime = _endTime.sort()
  for(let i = 1; i < startTime.length; i++) {
    if(startTime[i] <= endTime[i - 1]) {
      return false
    }
  }
  return true
}

/**
 * 
 * @param {Number} y
 * @param {Number} m
 * @param {Number} d
 * @returns {Number}
 */
export function getAllWeek(date) {
  let date_ = new Date(date)
  let week = date_.getDay(); //
  let allWeek = []
  let minus = week ? week - 1 : 6;
  let i = 0;
  while (i < 7) {
    let dd = new Date(date)
    dd.setDate(dd.getDate() - minus); //minus
    let y = dd.getFullYear();
    let m = (dd.getMonth() + 1 < 10 ? '0' + (dd.getMonth() + 1) : dd.getMonth() + 1);
    let d = dd.getDate() < 10 ? ('0' + dd.getDate()) : dd.getDate();
    allWeek.push(y + "-" + m + "-" + d)
    minus--;
    i++;
  }
  return allWeek
}

/**
 * @description: key
 * @param {type} byeKey true key   falsekey
 * @return: 
 */
export function objectToArray(obj, byeKey) {
  let arrList = []
  for(let key in obj) {
    if(byeKey) {
      arrList.push(key)
    }else {
      arrList.push(obj[key])
    }
  }
  return arrList
}

/**
 * @description: 
 * @param {type} 
 * @return: 
 */
export function changeDateToTimestamp(dateStr) {
  if(dateStr) {
    let dateTime = dateStr.replace(/-/g, '/')
    return Date.parse(new Date(dateTime))
  }else {
    return ''
  }
}

/**
 * @description: excel 
 * @param {type} columnNumber 
 * @return {type} 
 */
export function indexToColName(columnNumber) {
  let dividend = columnNumber;
  let columnName = '';
  let modulo = 0
  while (dividend > 0) {
    modulo = (dividend - 1) % 26;
    columnName = String.fromCharCode(65 + modulo) + columnName;
    dividend = parseInt((dividend - modulo) / 26);
  } 
  return columnName;
}



