import Vue from 'vue'
import Router from 'vue-router'
import Storage from '@/utils/localStorage'
import * as utils from '../utils/utils'
import Layout from '@/components/Index'
import Home from '@/components/Home'
import Login from '@/components/Login'

// 
import errorRouterList from './errorRouter'
// 
import orderMgtRoutersList from './orderMgtRouters'
// 
import goodsMgtRoutersList from './goodsMgtRouters'
// 
import activityList from './activity'
// 
import storeMgtRoutersList from './storeMgtRouters'
// 
import mallConfigRoutersList from './mallConfigRouters'
// 
import sysSettingRoutersList from './sysSettingRouters'
// 
import reportRoutersList from './report'
// 
import commonReportRoutersList from './commonReportRouters'

Vue.use(Router)

const scrollBehavior = (to, from, savedPosition) => {
  if (savedPosition) {
    return savedPosition
  } else {
    const position = {}
    if (to.hash) {
      position.selector = to.hash
    }
    if (to.matched.some(m => m.meta.scrollToTop)) {
      position.x = 0
      position.y = 0
    }
    return position
  }
}

/**
 * [router ]
 * 1meta
 * 2namepathbreadcrumbList
 * 3metacantChecktrue
 * 4metahideSelectCitytrue
 * @type {Router}
 */
const router = new Router({
  mode: 'history',
  base: '/shop/',
  scrollBehavior,
  routes: [
    {
      path: '/login',
      name: '',
      meta: {
        title: '',
        cantCheck: true
      },
      component: Login
    },
    {
      path: '/',
      redirect: '/index',
      component: Layout,
      children: [
        {
          path: '/index',
          name: '',
          meta: {
            title: '',
            cantCheck: true,
            breadcrumbList: [
              {name: '', path: ''}
            ]
          },
          component: Home
        },
        {
          path: '/demo',
          name: '',
          meta: {
            title: '',
            cantCheck: true,
            breadcrumbList: [
              {name: '', path: ''},
              {name: '', path: ''}
            ]
          },
          component: () => import('@/components/HelloWord')
        },
        // 
        ...orderMgtRoutersList.orderMgtRouters,
        // 
        ...goodsMgtRoutersList.goodsMgtRouters,
        // 
        ...activityList.actRouter,
        // 
        ...storeMgtRoutersList.storeMgtRouters,
        // 
        ...mallConfigRoutersList.mallConfigRouters,
        // 
        ...sysSettingRoutersList.sysSettingRouters,
        // 
        ...reportRoutersList.reportRouter,
        // 
        ...commonReportRoutersList.commonReportRoutersList,

        //    
        ...errorRouterList.errorRouter
      ]
    },
    {
      path: '/*',
      redirect: '/error'
    }
  ]
})

/**
 * @description: 
 * @param {type} 
 * @return: 
 */
router.beforeEach((to, from, next) => {
  // console.log("to:", to)
  if(to.meta.cantCheck) {
    next()
  }else {
    if(checkRouter(to.path, to.params)) {
      next()
    }else {
      next('/noopt')
    }
  }
})

router.afterEach((to, from) => {
  if (to.meta.title) {
    document.title = to.meta.title
  }
})
/**
 * @description: 
 * @param {type} 
 * @return: 
 */
const checkRouter = (toPath) => {
  let allMenusList = Storage.get("allMenus") && Storage.get("allMenus").length > 0 ? Storage.get("allMenus") : []
  let cPath = `/${toPath.split('/')[1]}`
  return utils.isKeyInArray(cPath, allMenusList) || utils.isKeyInArray(toPath, allMenusList)
}

export default router



