//  === 
const ClearThirdPlaData = () => import('@/components/SysSetting/ClearThirdPlaData')
//  === 
const SysLog = () => import('@/components/SysSetting/SysLog')
//  === ApkMgt
const ApkMgt = () => import('@/components/SysSetting/ApkMgt')
//  === ApkMgt 
const AddApk = () => import('@/components/SysSetting/ApkMgt/AddApk')
//  === ApkMgt 
const EditApk = () => import('@/components/SysSetting/ApkMgt/EditApk')

//  === 
const Suggest = () => import('@/components/SysSetting/Suggest')
// Redis ===   
const ClearData = () => import('@/components/SysSetting/ClearData')

const sysSettingRouters = [
  {
    path: '/cl_data',
    name: '',
    meta: {
      title: '',
      scrollToTop: true,
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: ''}
      ]
    },
    component: ClearThirdPlaData
  },
  {
    path: '/sys_log',
    name: '',
    meta: {
      title: '',
      scrollToTop: true,
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: ''}
      ]
    },
    component: SysLog
  },
  {
    path: '/apk',
    name: 'Apk',
    meta: {
      title: 'Apk',
      scrollToTop: true,
      breadcrumbList: [
        {name: '', path: ''},
        {name: 'Apk', path: ''}
      ]
    },
    component: ApkMgt
  },
  {
    path: '/apk/add',
    name: 'Apk',
    meta: {
      title: 'Apk',
      scrollToTop: true,
      breadcrumbList: [
        {name: '', path: ''},
        {name: 'Apk', path: '/apk'},
        {name: 'Apk', path: ''}
      ]
    },
    component: AddApk
  },
  {
    path: '/apk/edit',
    name: 'Apk',
    meta: {
      title: 'Apk',
      scrollToTop: true,
      breadcrumbList: [
        {name: '', path: ''},
        {name: 'Apk', path: '/apk'},
        {name: 'Apk', path: ''}
      ]
    },
    component: EditApk
  },
  {
    path: '/suggest',
    name: '',
    meta: {
      title: '',
      scrollToTop: true,
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: ''}
      ]
    },
    component: Suggest
  },
  {
    path: '/clear',
    name: 'Redis',
    meta: {
      title: 'Redis',
      scrollToTop: true,
      breadcrumbList: [
        {name: '', path: ''},
        {name: 'Redis', path: ''}
      ]
    },
    component: ClearData
  }
]

export default {
  sysSettingRouters
}




